import crypto from 'crypto'

/**
 * Check if an Id is rolled out or not
 */
export function isRolledOut(id: string, rolloutPercent: number): boolean {
  const hash = crypto.createHash('sha256')
  hash.update(id)
  const buf = hash.digest()

  // Take the first four bytes of the hash and convert them to an int
  const hashInt = buf.readUIntBE(0, 4)

  // Get the remainder of the int value from 100 to downsample it to a percent
  // then check if it's less than the current rolloutPercent
  return rolloutPercent > (hashInt % 100) + 1
}
